import * as React from 'react';
import Layout from '../components/Layout';
import { graphql } from 'gatsby';
import { GatsbyImage, getImage } from 'gatsby-plugin-image';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';

const AppsPage = ({ data }) => {
  const apps = data.allAppJson.nodes;

  return (
    <Layout>
      <Box sx={{ maxWidth: 1200, mx: 'auto', p: 3 }}>
        {/* Page Title */}
        <Typography variant="h3" component="h1" gutterBottom align="center">
          Apps Gallery
        </Typography>

        {/* Apps Grid */}
        <Grid container spacing={4}>
          {apps.map((app) => {
            const image = getImage(app.featuredImage);
            return (
              <Grid item key={app.id} xs={12} sm={6} md={4}>
                <Card sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}>
                  {/* Featured Image */}
                  <CardMedia>
                    <GatsbyImage
                      image={image}
                      alt={app.title}
                      style={{ width: '100%', height: '200px', objectFit: 'cover' }}
                    />
                  </CardMedia>

                  {/* App Title and Description */}
                  <CardContent sx={{ flexGrow: 1 }}>
                    <Typography variant="h5" component="h2" gutterBottom>
                      {app.title}
                    </Typography>
                    <Typography variant="body1" color="text.secondary">
                      {app.description}
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            );
          })}
        </Grid>
      </Box>
    </Layout>
  );
};

export default AppsPage;

// GraphQL query to fetch apps data from JSON
export const query = graphql`
  query AppsQuery {
    allAppJson {
      nodes {
        id
        title
        description
        featuredImage {
          childImageSharp {
            gatsbyImageData(width: 400, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
          }
        }
      }
    }
  }
`;